body {
  margin: 0;
}

.app {
  margin: 0;
  padding: 10px 20px;
  box-sizing: border-box;
  background-color: var(--tg-theme-bg-color);
  color: var(--tg-theme-text-color);
  width: 100%;
  height: 100vh;
  display: flex;
}

.header {
  display: flex;
  height: 30px;
  align-items: center;
  justify-content: space-between;
  width: 100%;

  &__caption {
    font-family: sans-serif;
    font-size: 22px;
    line-height: 24px;
  }

  &__exit-btn {
    padding: 0;
    margin: 0;
    background-color: transparent;
    border: none;
    font-size: 14px;
    line-height: 16px;
    color: var(--tg-theme-link-color);
  }
}
